import React, {useState} from 'react'
import s from "./Paginator.module.css";

const Paginator = ({totalUsersCount, pageSize, currentPage, onPageChanged, portionSize}) => {

    let pagesCount = Math.ceil(totalUsersCount / pageSize);

    let pages = [];
    for (let i = 1; i <= pagesCount; i++)
        pages.push(i);

    let portionCount = Math.ceil(pagesCount/portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPagePortionNumber = (portionNumber - 1) * portionSize + 1;
    let rightPagePortionNumber = portionNumber * portionSize;

    return (
        <div className={s.paginator}>
            {portionNumber > 1 && <span className={s.arrow} onClick={() => {setPortionNumber(portionNumber - 1)}}>{`<<`}</span> }
            <div>
                {pages
                    .filter(p => p >= leftPagePortionNumber && p <= rightPagePortionNumber)
                    .map(p => {
                    return (
                        <span className={`${currentPage === p && s.selectedPage} ${s.pageCursor} ${s.pageList}`}
                              key={p}
                              onClick={(e) => {
                                  onPageChanged(p);
                              }}>{`${p} `}</span>)
                })}
            </div>
            {portionCount > portionNumber  && <span className={s.arrow} onClick={() => {setPortionNumber(portionNumber + 1)}}>{`>>`}</span> }
        </div>
    )
}

export default Paginator;