const SEND_MESSAGE = 'SEND-MESSAGE';

let initState = {
    messages: [
        {id: 1, message: 'Hi'},
        {id: 2, message: 'How are you?'},
        {id: 3, message: 'Yo'},
        {id: 4, message: 'Hi'},
    ],
    dialogs: [
        {id: 1, name: 'Eugene'},
        {id: 2, name: 'Andrey'},
        {id: 3, name: 'Viktor'},
        {id: 4, name: 'Vasiliy'},
        {id: 5, name: 'Ivan'},
        {id: 6, name: 'Timofey'}
    ]
}

const dialogReducer = (state = initState, action) => {

    switch (action.type) {
        case SEND_MESSAGE:
            let messageText = action.newMessageText;
            return {
                ...state, newMessageText: '', messages: [...state.messages, {id: 5, message: messageText}]
            }
        default:
            return state;
    }
}

export const sendMessageCreator = (newMessageText) => ({type: SEND_MESSAGE, newMessageText})

export default dialogReducer;