import React from 'react';
import {Field, reduxForm} from "redux-form";
import {Element} from "../../common/FormControls/FormControls";

const Input = Element('input');
const Textarea = Element('textarea');

const ProfileDataForm = ({handleSubmit, profile}) => {
    return(
        <form onSubmit={handleSubmit}>
            <div>
                <b>FullName</b>:
                <Field component={Input} type={'text'} name={'fullName'} placeholder={'Full Name'}/>
            </div>
            <div>
                <b>Looking for a job</b>:<Field component={Input} type={'checkbox'} name={'lookingForAJob'}/>
            </div>
            <div>
                <b>My professional skills</b>:
                <div><Field component={Textarea} name={'lookingForAJobDescription'}/> </div>
            </div>
            <div>
                <b>About me</b>:
                <div><Field component={Textarea} name={'aboutMe'}/> </div>
            </div>
            <div><button>Save</button></div>
            {/*<div>
                <b>Contacts</b>: {Object.keys(profile.contacts).map(key => {
                return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]} />
            })}
            </div>*/}
        </form>
    )
}

const ProfileEditDataForm = reduxForm({form:'edit-profile'})(ProfileDataForm);

export default ProfileEditDataForm;