import React from 'react'
import Paginator from "../common/Paginator/Paginator";
import User from "./User";

const Users = ({totalUsersCount, pageSize, currentPage, onPageChanged, users, ...props}) => {

    return (
        <div>

            <Paginator onPageChanged={onPageChanged} totalUsersCount={totalUsersCount}
                       pageSize={pageSize} currentPage={currentPage} portionSize={20}/>
            {users.map(u => <User user={u} followingInProgress={props.followingInProgress} key={u.id}
                                        unfollowUsers={props.unfollowUsers} followUsers={props.followUsers}
            />
            )}
        </div>
    )
}

export default Users;