import {profileApi, usersAPI} from "../api/api";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET-USER-PROFILE';
const SET_USER_STATUS = 'SET-USER-STATUS';
const DELETE_POST = 'DELETE-POST';
const SAVE_PHOTO_SUCCESS = 'SAVE_PHOTO_SUCCESS';

let initState =  {
    posts: [
        {id: 1, message: 'Hi, how are you', likeCount: '100'},
        {id: 2, message: 'It\'s my first post', likeCount: '150'}
    ],
        profile: null,
        status: ''
}

const profileReducer = (state = initState, action) => {
    switch (action.type) {
        case ADD_POST:
            let newPost = {
                id: 3,
                message: action.addNewPost,
                likeCount: 0
            }

            return {
                ...state, posts: [...state.posts, newPost]
            };
        case SET_USER_PROFILE:
            return {...state, profile: action.profile}
        case SET_USER_STATUS:
            return {...state, status: action.status}
        case DELETE_POST:
            return {...state, posts: state.posts.filter(p => p.id != action.postId)}
        case SAVE_PHOTO_SUCCESS:
            return {...state, profile: {...state.profile, photos: action.photos}}
        default:
            return state;
    }
}

export const addPostActionCreator = (addNewPost) => ({type: ADD_POST, addNewPost})
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile})
export const setUserStatus = (status) => ({type: SET_USER_STATUS, status})
export const deletePost = (postId) => ({type: DELETE_POST, postId})
export const savePhotoSuccess = (photos) => ({type: SAVE_PHOTO_SUCCESS, photos})

export const getUserProfile = userId => {
    return (dispatch) => {
        usersAPI.userProfile(userId).then(data => {
                dispatch(setUserProfile(data));
            });
    }
}

export const getUserStatus = userId => {
    return async (dispatch) => {
        let response = await profileApi.getStatus(userId)
            dispatch(setUserStatus(response.data))
    }
}

export const updateUserStatus = status => {
    return (dispatch) => {
        profileApi.updateStatus(status).then(response => {
            if (response.data.resultCode === 0) {
                dispatch(setUserStatus(status))
            }
        })
    }
}

export const savePhoto = file => {
    return (dispatch) => {
        profileApi.savePhoto(file).then(response => {
            if (response.data.resultCode === 0) {
                dispatch(savePhotoSuccess(response.data.data.photos))
            }
        })
    }
}

export const saveProfile = profile => {
    return (dispatch) => {
        profileApi.saveProfile(profile).then(response => {
            if (response.data.resultCode === 0) {
                /*dispatch(savePhotoSuccess(response.data.data.photos))*/
                console.log(response.data.resultCode)
            }
        })
    }
}

export default profileReducer;