import React from 'react'
import {Field, reduxForm} from "redux-form";
import {connect} from "react-redux";
import {login} from "../../redux/auth-reducer";
import {required} from "../../utils/validators/validator_1";
import {Element} from "../common/FormControls/FormControls";
import {Redirect} from "react-router-dom";
import style from '../common/FormControls/FormControls.module.css'

const Input = Element('input')

const LoginForm = (props) => {

    return (
        <form onSubmit={props.handleSubmit}>
            <div><Field placeholder={"Email"} name={"email"} component={Input}
                        validate={[required]}/></div>
            <div><Field placeholder={"Password"} name={"password"} type={"password"} component={Input}
                        validate={[required]}/></div>
            <div><Field component={Input} name={"rememberMe"} type="checkbox"/>Remember me</div>
            {props.error && <div className={style.commonError}>{props.error}</div> }
            <div><button>LogIn</button></div>
        </form>
    )
}

const LoginUserForm = reduxForm({form: 'login'})(LoginForm)

const Login = (props) => {

    let onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe)
    }

    if (props.isAuth) {
        return <Redirect to={`/profile`} />
    }
    return(
        <div>
            <h1>Login</h1>
            <LoginUserForm onSubmit={onSubmit}/>
        </div>
    )
}
let mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth,
    userId: state.auth.userid
})

export default connect(mapStateToProps, {login})(Login);