import React from 'react';
import {connect} from "react-redux";
import {follow, followUsers, setCurrentPage, toggleFollowingProgress, unFollow, unfollowUsers, requestUsers
} from "../../redux/users-reducer";
import Users from "./Users";
import Preloader from "../common/Preloader/Preloader";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";
import {
    getPageSize,
    getTotalUsersCount,
    getCurrentPage,
    getIsFetching,
    getFollowingInProgress,
    getUsers
} from '../../redux/user-selectors'
import s from './Users.module.css'

class UsersContainer extends React.Component {

    componentDidMount() {
        this.props.requestUsers(this.props.currentPage, this.props.pageSize);
    }

    onPageChanged = (pageNumber) => {
        this.props.requestUsers(pageNumber, this.props.pageSize);
    }

    render() {

        return <>
            <div className={s.preloader}>{this.props.isFetching ? <Preloader /> : null}</div>
            <Users totalUsersCount = {this.props.totalUsersCount}
                   pageSize={this.props.pageSize}
                   currentPage={this.props.currentPage}
                   onPageChanged = {this.onPageChanged}
                   users={this.props.users}
                   followingInProgress={this.props.followingInProgress}
                   followUsers={this.props.followUsers}
                   unfollowUsers={this.props.unfollowUsers}
            />
        </>
    }
}


let mapStateToProps = (state) => {
    return {
        users: getUsers(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state)
    }
}

export default compose(
    connect(mapStateToProps, {
        follow, unFollow, followUsers, unfollowUsers, requestUsers, toggleFollowingProgress, setCurrentPage}),
        withAuthRedirect
)(UsersContainer)

