import React from 'react';
import s from "./MyPosts.module.css";
import Post from "./Post/Post";
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../../utils/validators/validator_1";
import {Element} from "../../common/FormControls/FormControls";

let maxLength10 = maxLengthCreator(10);
let Textarea = Element('textarea');

const MyPosts = (props) => {
    let postsElements = props.profilePage.posts.map(p => <Post message = {p.message} likeCount = {p.likeCount}/>)

    let addPost = (value) => {
        props.addPost(value.newPostText);
    }

    return (
        <div className={s.postBlock}>
            <h3>My Posts</h3>
            <div>
                <AddPostFormRedux onSubmit={addPost} />
            </div>

            <div className={s.posts}>
                {postsElements}
            </div>
        </div>
    )
}

const AddPostForm = (props) => {

    return(
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field component={Textarea} name={'newPostText'} placeholder={'Add Your post'}
                       validate={[required, maxLength10]}/>
            </div>
            <div>
                <button>Add Post</button>
            </div>
        </form>
    )
}

const AddPostFormRedux = reduxForm({form: 'newPostForm'})(AddPostForm)

export default MyPosts;