import s from "./Post.module.css";

const Post = (props) => {
    return (
        <div className={s.item}>
            <img src="http://www.youloveit.ru/uploads/posts/2020-04/1586360146_youloveit_ru_bill_gravity_falls_na_avu09.jpg"/>
            {props.message}
            <div>
                <span>Like {props.likeCount}</span>
            </div>
        </div>
    )
}

export default Post;